import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class AuthorizationService {
  userLocal = null; // user,  ..  -  LocalStorage
  currentUser = null; ///     firebase
  loggedIn: Subject<any>  = new Subject<any>(); //  
  checkIn: Subject<any>  = new Subject<any>(); //   ,    

  constructor(
    private router: Router
  ) { }

  userLocalStorage(user): void{
    localStorage.setItem('activeUser', JSON.stringify(user)); //   localStorage
    this.loggedIn.next(user); //   
  }
}
